import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import monthPicker from 'c%/MonthPicker.vue'
export default{
	mixins: [common],
	components: {
		monthPicker
	},
	data () {
		return {
			data_uri: '',
			data: []
		}
	},
	mounted () {
		this.data_uri = 'students/'+this.sid$+'/credit_history'
	},
	methods: {
		hasTitle (index) {
			if(index > 0){
				return moment(this.data[index].create_time).format('YYYYMM') != moment(this.data[index-1].create_time).format('YYYYMM')
			}
			return index==0
		},	
		getMonth (monthIndex){
			let result = String(this.data[monthIndex].create_time)
			
			return moment(result).format('MM月')
		},
		showMonth (time) {
			time = moment(time).format('YYYYMMDD')
			console.log('time:'+time)
			this.$refs['month_picker'].show(time)
		},
		formatDate (row) {
			return moment(row).format('MM/DD')
		},
		onChange (time) {
			this.data_uri = util.sprintf('students/%s/credit_history?year_month=%s',this.sid$,time)
			this.pageIndex = 0
			this.mescroll.resetUpScroll()
		},			
	},
	computed: {
		credit () {
			return parseInt(this.student$.credit)
		}
	}
}



